.logo {
  height: 50px;
  width: auto;
}

.logo > img {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

body {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
}

main {
  flex: 1 0 auto;
}

.collapsible-body {
  background-color: white;
}

.link-text {
  color: #2196f3;
  text-decoration: underline;
  cursor: pointer;
}
